import { marked } from "marked";

/**
 * This function will treat the text provided as plain text and insert it into word document
 * @param {string} text The text to be inserted
 */
export async function insertPlainText(text) {
  // Write text to the document.
  try {
    if (typeof Word !== "undefined") {
      await Word.run(async (context) => {
        let body = context.document.body;
        body.insertParagraph(text, Word.InsertLocation.end);
        await context.sync();
      });
    } else {
      console.log(text);
    }
  } catch (error) {
    console.log("Error: " + error);
  }
}

/**
 * Converts Markdown content to HTML and inserts it into the Word document.
 * @param {string} markdownContent The Markdown string to insert.
 */
export const insertMarkdownAsHtml = async (markdownContent) => {
  try {
    // Convert Markdown to HTML
    const htmlContent = marked.parse(markdownContent);
    // marked.parse returns a string like "<p>Hello</p><h2>Heading</h2>"

    if (typeof Word !== "undefined") {
      await Word.run(async (context) => {
        // Optional: Insert a new paragraph before the content for better spacing
        context.document.body.insertParagraph("", Word.InsertLocation.end);

        // Insert the generated HTML into the document body at the end
        context.document.body.insertHtml(htmlContent, Word.InsertLocation.end);

        await context.sync();
        console.log("Markdown content converted to HTML and inserted into Word successfully.");
      });
    } else {
      console.log(htmlContent);
    }
  } catch (error) {
    console.error("Error inserting Markdown as HTML into Word:", error);
    // Propagate the error so HomePage can catch it
    throw error;
  }
};

/**
 * Generate a new word document with provided contents.
 * @param {string[]} markdownContents The markdown contents (array) to be inserted into the new document
 */
export const generateWordDocWithContents = async (markdownContents) => {
  if (typeof Word === "undefined") {
    console.log(markdownContents);
    return;
  }

  await Word.run(async (context) => {
    const doc = context.application.createDocument();
    for (const markdownContent of markdownContents) {
      const htmlContent = marked.parse(markdownContent);
      doc.body.insertHtml(htmlContent, Word.InsertLocation.end);
      doc.body.insertParagraph("", Word.InsertLocation.end);
      await context.sync();
    }
    doc.open();
  });
};
